.pagina__transacciones-componente {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 125px;

  padding: 20px;

  border-bottom: 1px solid gray;
  

  justify-content: space-between;
  cursor: pointer;
  transition: all .3s ease;
}

.pagina__transacciones-componente:hover {
  background-color: rgb(241, 241, 241);
}

.transacciones-icono {
  cursor: pointer;
  color: red;
  transition: all .3s ease;
  visibility: hidden;
}

.transacciones-icono:hover {
  transform: scale(1.1);
  color: rgb(158, 29, 29);
}

.pagina__transacciones-componente:hover .transacciones-icono{
  visibility: visible;
}



.pagina__transacciones-componente-contenedor-izquierda {
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
}

.pagina__transacciones-componente-contenedor-izquierda p {
  color: #6e8efb;
  font-size: 20px;
}

.pagina__transacciones-componente-contenedor-izquierda span {
  color: gray;
  font-size: 16px;
} 

.pagina__transacciones-componente-contenedor-derecha {
  display: flex;
  align-items: center;
  gap: 10px;
}


.transaccion-positiva {
  color: green;
}

.transaccion-negativa {
  color: red;
}

@media (min-width: 600px) {
  .pagina__transacciones-componente {
    flex-direction: row;
  }
}