.pagina__transacciones {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: calc(100vh - 120px);
  max-width: 1200px;
  margin: auto;

  padding: 20px;
  gap: 20px;

  align-items: center;
}

.pagina__transacciones-titulo {
  color: #6e8efb;
}


.pagina__transacciones-contenedor {
  display: flex;
  flex-direction: column-reverse;
  width: 90%;
  min-height: 200px;

  padding: 10px;

  background-color: white;
  border-radius: 20px;
  box-shadow: 0px 5px 10px 0px gray;

  align-items: center;
  justify-content: center;
  text-align: center;
}

.pagina__transacciones-boton {
  display: flex;
  width: 200px;
  height: 30px;

  border-radius: 10px;
  background-color: red;
  border: none;
  color: white;

  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all .3s ease;
}

.pagina__transacciones-boton:hover {
  background-color: rgb(158, 16, 16);
  transform: translateY(-5px);
}