.pagina__detalles-componente-desglose-gastos-categorias {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 700px;
    min-height: 400px;
  
    padding: 20px;
    gap: 20px;
  
    background-color: white;
    border-radius: 20px;
    box-shadow: 0px 5px 10px 0px gray;
  
    align-items: center;
    transition: all .3s ease;
  }
  
.pagina__detalles-componente-desglose-gastos-categorias:hover {
  transform: translateY(-10px); 
}


.pagina__detalles-componente-grafico-contenedor-h1 {
  display: flex;
  width: 100%;
  min-height: 250px;

  align-items: center;
  justify-content: center;

  font-size: 30px;
  text-align: center;
}
