.pagina__detalles-componente-resumen {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1000px;
  min-height: 150px;

  padding: 20px;
  gap: 30px;

  border-radius: 20px;
  background-color: white;

  box-shadow: 0px 5px 10px 0px gray;


  align-items: center;
  transition: all .3s ease;
}

.pagina__detalles-componente-resumen:hover {
  transform: translateY(-10px);
}

/* grilla  */

.pagina__detalles-componente-resumen-contenedor-grilla {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fill,minmax(200px, 1fr));
  gap: 20px;

  justify-content: center;
  align-items: center;
}

.pagina__detalles-componente-resumen-contenedor-items {
  display: flex;
  flex-direction: column;
  width: 100%;
  
  gap: 10px;

  align-items: center;
  justify-content: center;
}

.pagina__detalles-componente-resumen-contenedor-item {
  color: rgb(69, 107, 196);
  font-weight: 600;
}

.positivo {
  color: green;
}

.negativo {
  color:red;
}


.pagina__detalles-componente-resumen-contenedor-items span {
  font-size: 16px;
  color: gray;
}
