.pagina__detalles-componente-ultimas-transacciones {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1000px;
  min-height: 200px;

  padding: 20px;
  gap: 10px;

  background-color: white;
  box-shadow: 0px 5px 10px 0px gray;
  border-radius: 20px;

  align-items: center;
  transition: all .3s ease;
}

.pagina__detalles-componente-ultimas-transacciones:hover {
  transform: translateY(-10px);
}

.pagina__detalles-componente-ultimas-transacciones-contenedor {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 150px;
  align-items: center;
  justify-content: center;
}

.pagina__detalles-componente-ultimas-transacciones-contenedor h1 {
  font-size: 30px;
  text-align: center;
}
